  "DOTA_Tooltip_ability_antimage_mana_break_Lore": "一种格斗技巧，改良自星隐寺僧侣们的温和招式，可以让魔法能量反噬其拥有者。",
  "DOTA_Tooltip_ability_antimage_mana_break_mana_per_hit": "每次攻击损毁魔法：",
  "DOTA_Tooltip_ability_antimage_blink": "闪烁",
  "DOTA_Tooltip_ability_antimage_blink_Description": "让敌法师自由出入战场的短距离传送。",
  "DOTA_Tooltip_ability_antimage_blink_Lore": "在与死亡之神遭遇时，敌法师明白了让人捉摸不定是多么重要。",
  "DOTA_Tooltip_ability_antimage_blink_blink_range": "距离：",
  "DOTA_Tooltip_ability_antimage_spell_shield": "法术护盾",
  "DOTA_Tooltip_ability_antimage_spell_shield_Description": "提高敌法师的魔法抗性。\\n\\n可用神杖升级。",
  "DOTA_Tooltip_ability_antimage_spell_shield_Lore": "多年的冥想和对复仇的执着强化了敌法师的皮肤，可以更好地抵御神秘的对手。",
  "DOTA_Tooltip_ability_antimage_spell_shield_spell_shield_resistance": "%魔法抗性：",
