  "DOTA_Tooltip_ability_obsidian_destroyer_sanity_eclipse_cast_range": "施法距离：",
  "DOTA_Tooltip_ability_lycan_summon_wolves": "召狼",
  "DOTA_Tooltip_ability_lycan_summon_wolves_Lore": "扭曲他生命本质的魔法还能召唤出他的犬科伙伴。",
  "DOTA_Tooltip_ability_lycan_summon_wolves_Description": "召唤两头狼为狼人而战。等级3-4的狼拥有致残技能，等级4的狼还拥有永久隐身技能。",
  "DOTA_Tooltip_ability_lycan_summon_wolves_wolf_duration": "持续时间：",
