  "DOTA_Tooltip_ability_lycan_summon_wolves_wolf_hp": "生命值：",
  "DOTA_Tooltip_ability_lycan_howl": "嗥叫",
  "DOTA_Tooltip_ability_lycan_howl_Lore": "毛骨悚然的狼嚎让敌人明白贝恩霍勒在就在它们之中。",
  "DOTA_Tooltip_ability_lycan_howl_Description": "给予狼人、所有友方英雄及所有玩家控制的单位攻击力加成。嗥叫在夜晚效果加倍。",
  "DOTA_Tooltip_ability_lycan_howl_howl_duration": "持续时间：",
