  "DOTA_Tooltip_ability_morphling_replicate_tooltip_cast_range": "施法距离：",
  "DOTA_Tooltip_ability_phantom_lancer_spirit_lance_Lore": "阿兹瑞斯对于用长矛捕鱼养家的精通被证明在战场上非常有用。",
  "DOTA_Tooltip_ability_phantom_lancer_spirit_lance_duration": "减速持续时间：",
