  "DOTA_Tooltip_ability_phantom_lancer_doppelwalk_Lore": "恐怖巫师沃恩的死亡让幻影长矛手获得了将自身在所有光线之中弯折破碎的能力。",
  "DOTA_Tooltip_ability_phantom_lancer_doppelwalk_illusion_duration": "神行幻象持续时间：",
  "DOTA_Tooltip_ability_phantom_lancer_juxtapose_Lore": "阿兹瑞斯的每次刺击感觉就像一个普通战士的两次，或三次，或四次……",
  "DOTA_Tooltip_ability_spirit_breaker_nether_strike_tooltip_range": "范围：",
