  "DOTA_Tooltip_ability_lycan_summon_wolves_invisibility_Description": "不进行攻击时永久隐身。",
  "DOTA_Tooltip_ability_brewmaster_thunder_clap": "雷霆一击",
  "DOTA_Tooltip_ability_brewmaster_thunder_clap_Lore": "曼吉克斯巨大酒桶的一击标志着庆典的开始。",
  "DOTA_Tooltip_ability_brewmaster_thunder_clap_Description": "猛击地面，造成伤害并降低附近敌方地面单位的移动速度和攻击速度。",
  "DOTA_Tooltip_ability_brewmaster_thunder_clap_radius": "作用范围：",
