  "DOTA_Tooltip_ability_brewmaster_drunken_haze": "醉酒云雾",
  "DOTA_Tooltip_ability_brewmaster_drunken_haze_Lore": "奥尤社解决所有问题的方法 - 再喝一轮！",
  "DOTA_Tooltip_ability_brewmaster_drunken_haze_Description": "用酒精浸透目标，使其移动速度降低，并且攻击有一定概率不能命中。",
  "DOTA_Tooltip_ability_brewmaster_drunken_haze_duration": "持续时间：",
