  "DOTA_Tooltip_ability_brewmaster_drunken_brawler": "醉拳",
  "DOTA_Tooltip_ability_brewmaster_drunken_brawler_Lore": "曼吉克斯赢得奥尤社的酒仙称号的同时，也确立了他在精通醉拳方面的地位。",
  "DOTA_Tooltip_ability_brewmaster_drunken_brawler_Description": "有一定概率闪避攻击并能造成致命一击。只要在若干秒内没有发动攻击，或没有受到攻击，醉拳的被动效果将下一次攻击或被攻击时必定触发。",
