  "DOTA_Tooltip_ability_keeper_of_the_light_spirit_form_Description": "伊扎洛暂时将自己的身体转换为发光的形态，获得多种能力。冲击波由一个独立的灵魂来积聚，同时伊扎洛获得致盲之光和召回技能。\\n\\n可用神杖升级。",
