  "DOTA_Tooltip_ability_visage_summon_familiars_Description": "召唤2只盲眼的佣兽为维萨吉作战。佣兽拥有石像形态技能，可以化为石像，在落地时使敌人眩晕。在石像形态中的佣兽无敌，并且迅速回复生命值。佣兽被击杀时给予高额奖励。\\n\\n可用神杖升级。",
