  "DOTA_Tooltip_ability_gyrocopter_homing_missile_Lore": "奥雷尔的飞行器上所能装载的最大的导弹，为了实现最佳效果，他对空气动力学和爆炸物进行精心计算才得到了炸蛋™。",
  "DOTA_Tooltip_ability_gyrocopter_flak_cannon_Lore": "这架新近改造的旋翼机装备了足够的弹药，可以攻击360度全范围内的敌人。",
