  "DOTA_Tooltip_ability_lina_laguna_blade_aghanim_description": "伤害类型变为纯粹，并且可以对技能免疫状态下的单位施放神灭斩。",
  "DOTA_Tooltip_ability_furion_wrath_of_nature_aghanim_description": "提高伤害。被自然之怒杀死，或受到自然之怒伤害后4秒内死亡的单位都会变成树人，生命值和攻击力与当前等级的自然的呼唤相同。如果杀死的是英雄，将会产生大树人，生命值和攻击力是普通的3倍。",
  "DOTA_Tooltip_ability_windrunner_focusfire_cooldown_scepter": "神杖升级冷却时间：",
  "DOTA_Tooltip_ability_windrunner_focusfire_aghanim_description": "减少冷却时间，改善降低攻击力效果，物品效果的伤害不会降低。",
  "DOTA_Tooltip_ability_lion_finger_of_death_aghanim_description": "提高伤害，减少冷却时间。死亡之指将作用一小片区域内的所有单位。",
  "DOTA_Tooltip_ability_vengefulspirit_nether_swap_nether_swap_cooldown_scepter": "神杖升级冷却时间：",
  "DOTA_Tooltip_ability_vengefulspirit_nether_swap_aghanim_description": "减少冷却时间，并可以对非英雄单位施放。复仇之魂死亡时将产生复仇幻象，幻象持续到她复活，而且可以使用所有技能（不包括物品）。",
  "DOTA_Tooltip_ability_witch_doctor_death_ward_aghanim_description": "死亡守卫的攻击拥有克敌机先效果，并且会向附近英雄弹射。",
  "DOTA_Tooltip_ability_juggernaut_omni_slash_aghanim_description": "增加斩击次数并减少冷却时间。",
  "DOTA_Tooltip_ability_razor_eye_of_the_storm_aghanim_description": "提高打击速度，并且能对建筑造成伤害。打击建筑时，只会以防御塔、兵营和遗迹作为目标。",
  "DOTA_Tooltip_ability_crystal_maiden_freezing_field_aghanim_description": "对极寒领域内停留超过2.5秒的所有单位施加冰封禁制。",
