  "DOTA_Tooltip_ability_winter_wyvern_arctic_burn_Description": "寒冬飞龙乘着一股极地寒风扶摇而上，移动将无视地形，每次攻击都将呼出逼人的寒气。高飞时，她的攻击射程更远，攻击弹道速度也更快，被攻击的敌人在寒意作用下，移动速度也被减缓，同时每秒损失%percent_damage%%%的当前生命值。在夜晚的寒风中，她的视野还会被加强，在技能期间夜晚将额外获得%night_vision_bonus%的视野范围。\\n\\n可用神杖升级。",
