  "DOTA_Tooltip_ability_mud_golem_rock_destroy_Description": "死亡时，泥土傀儡破碎成2个碎土傀儡，拥有%shard_health_tooltip%点生命值，%shard_damage_tooltip%点攻击力，存活%shard_duration_tooltip%秒。",
  "DOTA_Tooltip_ability_item_abyssal_blade_Note2": "以下英雄无法触发该物品的重击效果：裂魂人，虚空假面，斯拉达，以及巨魔战将。",
  "DOTA_Tooltip_ability_item_basher_Note1": "以下英雄无法触发该物品的重击效果：裂魂人，虚空假面，斯拉达，以及巨魔战将。",
  "DOTA_Tooltip_ability_item_gem_Lore": "只为苍生不受深海的奴役\\n只为生魂免去溺灵要塞的灾戾\\n只为让麦尔朗恩长眠于海底\\n璀璨的宝石终归于沉寂",
  "DOTA_Tooltip_ability_item_aether_staff_Description": "&nbsp;",
  "DOTA_Tooltip_ability_item_aether_staff_Lore": "&nbsp;",
  "DOTA_Tooltip_ability_item_moon_shard_Description": "<h1>使用：吞噬</h1> 吞噬银月之晶，永久获得%consumed_bonus%攻击速度和%consumed_bonus_night_vision%夜间视野加成。最多使用1次。\\n<h1>被动：月夜之视</h1>提供额外%bonus_night_vision%夜间视野。",
  "DOTA_Tooltip_ability_item_moon_shard_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_moon_shard_Note0": "额外夜间视野在暗夜魔王的黑暗时间技能下同样生效。",
  "DOTA_Tooltip_ability_item_moon_shard_Lore": "相传这是月之女神赛莉蒙妮流下的一滴眼泪。",
  "DOTA_Tooltip_ability_item_silver_edge_Description": "<h1>主动：暗影步</h1>进入隐身直到%windwalk_duration%秒持续时间结束、攻击或施法。隐身时，移动速度将提升%windwalk_movement_speed%%%，而且可以穿越单位。<br><br>破隐一击将额外造成%windwalk_bonus_damage%点物理伤害， 并且%backstab_duration%秒内目标的被动技能无效。",
  "DOTA_Tooltip_ability_item_silver_edge_bonus_damage": "+$damage",
  "DOTA_Tooltip_ability_item_silver_edge_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_silver_edge_Lore": "曾经是手刃不义之君的利器，但是后来王国也因此陷入了内乱。",
  "DOTA_Tooltip_ability_item_silver_edge_Note0": "暗影步无法闪避。",
  "DOTA_Tooltip_ability_item_silver_edge_Note1": "暗影步并不能无视技能免疫。",
  "DOTA_Tooltip_ability_item_glimmer_cape_Description": "<h1>主动： 微光</h1> 经过%fade_delay%秒延迟后，自身或者友方目标单位进入隐身状态， 获得%active_magical_armor%%%的魔法抗性加成。持续%duration%秒。<br><br>施法距离：%tooltip_range%<br><br>可以在持续施法时施放。",
  "DOTA_Tooltip_ability_item_glimmer_cape_bonus_attack_speed": "+$attack",
  "DOTA_Tooltip_ability_item_glimmer_cape_bonus_magical_armor": "%+$spell_resist",
  "DOTA_Tooltip_ability_item_glimmer_cape_Lore": "一位幻术大师失窃的披风。",
  "DOTA_Tooltip_ability_item_red_mist_Description": "&nbsp;",
  "DOTA_Tooltip_ability_item_red_mist_Lore": "&nbsp;",
  "DOTA_Tooltip_ability_item_octarine_core_Description": "<h1>被动：冷却减少</h1>所有技能和物品的冷却时间减少%bonus_cooldown%%%。\\n<h1>被动：技能吸血</h1>技能对敌方英雄造成伤害的%hero_lifesteal%%%将以生命回复返还给施法者，对所有技能伤害均有效。<br><br>技能吸血对普通单位的效果为%creep_lifesteal%%%。",
  "DOTA_Tooltip_ability_item_octarine_core_bonus_intelligence": "+$int",
  "DOTA_Tooltip_ability_item_octarine_core_bonus_health": "+$health",
  "DOTA_Tooltip_ability_item_octarine_core_bonus_mana": "+$mana",
  "DOTA_Tooltip_ability_item_octarine_core_Lore": "位于法术技艺核心的，是只有身负异能之人才能感知的光带。",
  "DOTA_Tooltip_ability_item_enchanted_mango_Description": "<h1>使用：吞食芒果</h1>立刻回复%replenish_amount%点魔法。<br><br>对友军施放距离：%range_tooltip%",
  "DOTA_Tooltip_ability_item_enchanted_mango_hp_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_enchanted_mango_Lore": "基迪岛上特有的苦甜参半风味让两栖动物无法抗拒。",
  "DOTA_Tooltip_ability_item_iron_talon_Description": "<h1>主动：砍伐</h1>对非玩家控制的敌方单位使用，移除当前生命值的%creep_damage_pct%%%。<br><br>对树木或守卫施放会立刻将其摧毁。 <br><br>砍伐单位施法距离：%tooltip_range%<br>砍伐树木施法距离：%quelling_range_tooltip%<BR>砍伐守卫施法距离：%cast_range_ward%\\n<h1>被动：压制</h1> 对非英雄单位进行物理攻击会造成额外伤害，近战英雄为%damage_bonus%点，远程英雄为%damage_bonus_ranged%点。",
  "DOTA_Tooltip_ability_item_iron_talon_Lore": "简单却有效的武器，为镇压地狱熊怪的起义而设计。",
  "DOTA_Tooltip_ability_item_lotus_orb_Description": "<h1>主动：回音护盾</h1>对目标单位施加一个护盾，持续%active_duration%秒，可以将大部分指向性法术反馈给施法者。<br><br>护盾目标依然会受到法术的伤害。<br><br>驱散类型：<span class=\\\"GameplayValues GameplayVariable\\\">弱驱散</span>",
  "DOTA_Tooltip_ability_item_lotus_orb_bonus_armor": "+$armor",
  "DOTA_Tooltip_ability_item_lotus_orb_bonus_health_regen": "+$hp_regen",
  "DOTA_Tooltip_ability_item_lotus_orb_bonus_mana_regen": "+$mana_regen",
  "DOTA_Tooltip_ability_item_lotus_orb_Lore": "位居中央的宝石依然能反射出其制造者苍白的景象。",
