  "DOTA_Tooltip_ability_item_guardian_greaves_Description": "<h1>主动：修复</h1>使周围友军回复%replenish_health%点生命和%replenish_mana%点魔法，并移除施法者身上大部分的负面效果。<br><br>作用范围：%replenish_radius%<br>驱散类型：<span class=\\\"GameplayValues GameplayVariable\\\">弱驱散</span>\\n<h1>被动：卫士光环</h1> 为周围友军提供%aura_health_regen%点/秒额外生命恢复和%aura_armor%点额外护甲。如果友方英雄的生命值低于%aura_bonus_threshold%%%，他们将获得%aura_health_regen_bonus%点/秒额外生命恢复和%aura_armor_bonus%点额外护甲。\\n多个鞋类物品提供的移动速度加成不叠加。",
