  "DOTA_Tooltip_ability_item_infused_raindrop_Description": "<h1>被动：魔法伤害抵挡</h1>受到超过%min_damage%点伤害时自动触发，消耗一点能量并抵挡%magic_damage_block%点魔法伤害。<br><br>自带%initial_charges%点能量。能量耗尽后消失。",
