  "DOTA_Tooltip_ability_item_bloodthorn_Description": "<h1>主动：灵魂撕裂</h1>使目标沉默%silence_duration%秒，沉默结束后其间所受伤害的%silence_damage_percent%%%将以额外魔法伤害的形式结算。<br><br>所有对目标造成的攻击均不会落空，并且造成%target_crit_multiplier%%%致命一击伤害。<br><br>施法距离：%tooltip_range%\\n<h1>被动：致命一击</h1>每次攻击时有%crit_chance%%%的概率造成%crit_multiplier%%%致命伤害。",
