  "DOTA_Tooltip_ability_item_river_painter_Description": "将药液倒入河水中，将河水转变成液态铬，持续15分钟。<font color='#FF5555'>在河道上使用这件物品并且持续时间结束时将永久消耗兵器库中相应物品的一点能量。在持续时间结束前药液效果被更强劲的药瓶所取代不会消耗能量。</font>药瓶只能在兵器库中物品还存在至少一点能量时使用。只有成功改变河水才会消耗能量，在比赛中出售、丢弃或摧毁物品不会消耗能量。若河水已被更强力的药液作用，将无法对河道使用药瓶。",
