  "DOTA_Tooltip_ability_item_aeon_disk_Description": "<h1>被动：强断连招</h1>当受到伤害并且生命值低于%health_threshold_pct%%时，会对英雄施加一个强驱散效果，%buff_duration%秒内所有受到和造成的伤害全部归零。来自玩家的伤害才会触发。",
