  "DOTA_Tooltip_ability_item_nullifier_Description": "<h1>主动：否决</h1>驱散目标身上的效果，并且施加锁闭，持续%mute_duration%秒。每次被锁闭的目标受到攻击都会被减速%slow_pct%%%，持续%slow_interval_duration%秒。<br><br>驱散类型：<span class=\\\"GameplayValues GameplayVariable\\\">弱驱散</span>",
  "DOTA_Tooltip_ability_item_nullifier_lore": "危险的武器，盗自一个反维度。",
  "DOTA_Tooltip_ability_item_nullifier_bonus_damage": "+$damage",
