  "DOTA_Tooltip_ability_item_spirit_vessel_Description": "<h1>主动：灵魂释放</h1> 对敌人使用时，每秒减少%enemy_hp_drain%%%当前生命值，并且生命恢复和治疗效果降低%hp_regen_reduction_enemy%%%。每秒造成%soul_damage_amount%点伤害。<br><br>对友军使用时，每秒增加%ally_hp_gain%%%当前生命值，并且生命恢复和治疗效果提升%hp_regen_increase_allies%%%。提供%soul_heal_amount%点/秒生命恢复。<br><br>持续%duration%秒。<br><br>每当敌方英雄在%soul_radius%范围内死亡时获得充能。只有最靠近阵亡英雄的灵瓮能得到充能。<br><br>施法距离：%soul_release_range_tooltip% ",
